.toggle {
  color: white;
  cursor: pointer;
  outline: none;
  font-weight: bold;
  font-size: 7vh;
  -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    margin-right: 3vh;
}

.toggled {
  color: white;
  cursor: pointer;
  outline: none;
  font-weight: bold;
  font-size: 7vh;
  -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotate(-315deg);
            transform: rotate(-315deg);
    margin-right: 14vh;

}

